.page1-container {
  background-color: rgb(216, 160, 5);
  min-height: 100vh;
  padding: 20px;
  box-sizing: border-box;
  font-family: "Inter", Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.page-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  color: #fff;
  text-align: center;
}

.help-icon {
  position: absolute;
  top: 15px;
  right: 15px;
  background: #2e2e2e;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background 0.2s;
}

.help-icon a {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.help-icon:hover {
  background: #444;
}

.select-image-btn {
  background: #2e2e2e;
  color: #fff;
  padding: 12px 24px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 25px;
  border: none;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2);
  transition: background 0.2s, transform 0.1s;
}

.select-image-btn:hover {
  background: #444;
  transform: scale(1.03);
}

.image-preview {
  margin: 20px 0;
  max-width: 100%;
  display: flex;
  justify-content: center;
}

.image-preview img {
  max-width: 340px;
  width: 100%;
  border-radius: 12px;
  border: 3px solid #2e2e2e;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

/* Contenedor respuesta en estilo dark card */
.page1-block {
  background: #2e2e2e;
  border-radius: 14px;
  padding: 18px;
  margin-top: 20px;
  max-width: 650px;
  width: 100%;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.25);
}

.page1-line {
  font-size: 1rem;
  color: #f1f1f1;
  line-height: 1.5;
}
